import React from "react";
import Hammer from "react-hammerjs";
import client from "../mqtt";

import { triggerAttack, triggerRelease } from "../store/reducers/band";

import styled from "styled-components";

const Container = styled.div`
  width: 100%;
  height: 100%;
  background-color: lightskyblue;
`;

const scale = [
  "C3",
  "D3",
  "E3",
  "F3",
  "G3",
  "A3",
  "B3",
  "C4",
  "D4",
  "E4",
  "F4",
  "G4",
  "A4",
  "B4",
];
const options = {
  touchAction: "compute",
  recognizers: {
    pan: {
      threshold: 0,
    },
  },
};

export default ({ active, type, prefix }) => {
  return (
    <>
      <Hammer
        options={options}
        onMouseDown={(event) => {
          if (!active) return;
          const x =
            (event.clientX - event.target.getBoundingClientRect().left) /
            event.target.offsetWidth;
          const y =
            1 -
            (event.clientY - event.target.getBoundingClientRect().top) /
              event.target.offsetHeight;
          client.publish(
            prefix,
            JSON.stringify(
              triggerAttack(
                type,
                scale[Math.floor(x * scale.length)],
                y / 2 + 0.5
              )
            )
          );
        }}
        onMouseUp={(event) => {
          if (!active) return;
          client.publish(prefix, JSON.stringify(triggerRelease(type)));
        }}
        onPan={(event) => {
          // const x = event.center.x;
          // const y = event.center.y;
          if (!active) return;
          const x =
            (event.center.x -
              event.srcEvent.target.getBoundingClientRect().left) /
            event.srcEvent.target.offsetWidth;
          const y =
            1 -
            (event.center.y -
              event.srcEvent.target.getBoundingClientRect().top) /
              event.srcEvent.target.offsetHeight;
          client.publish(
            prefix,
            JSON.stringify(
              triggerAttack(
                type,
                scale[Math.floor(x * scale.length)],
                y / 2 + 0.5
              )
            )
          );
        }}
        onPanStart={(event) => {
          if (!active) return;
          const x =
            (event.center.x -
              event.srcEvent.target.getBoundingClientRect().left) /
            event.srcEvent.target.offsetWidth;
          const y =
            1 -
            (event.center.y -
              event.srcEvent.target.getBoundingClientRect().top) /
              event.srcEvent.target.offsetHeight;

          const n = scale[Math.floor(x * scale.length)];
          client.publish(
            prefix,
            JSON.stringify(triggerAttack(type, n, y / 2 + 0.5))
          );
        }}
        onPanEnd={(event) => {
          if (!active) return;
          client.publish(prefix, JSON.stringify(triggerRelease(type)));
        }}
      >
        <Container></Container>
      </Hammer>
    </>
  );
};
